export async function run() {
  try {
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
    const fileInput = document.getElementById("fileInput");
    const files = fileInput.files;
    
     
    
    if (files.length === 0) {
      Swal.fire({
        title: "Error!",
        text: mitextoError,
        icon: "error",
        showConfirmButton: false,
        timer: 2000
      });
      //alert("Por favor selecciona al menos un archivo.");
      return;
    }
    try {
      let combinedData = [];

      for (let i = 0; i < files.length; i++) {
        const file = files[i];
        const arrayBuffer = await file.arrayBuffer();
        const workbook = XLSX.read(arrayBuffer, { type: "array" });
        const firstSheet = workbook.Sheets[workbook.SheetNames[0]];
        const data = XLSX.utils.sheet_to_json(firstSheet, { header: 1 });

        if (i === 0) {
          combinedData.push(...data);
        } else {
          combinedData.push(...data.slice(1));
        }
      }

      const newWorkbook = XLSX.utils.book_new();
      const newWorksheet = XLSX.utils.aoa_to_sheet(combinedData);
      XLSX.utils.book_append_sheet(newWorkbook, newWorksheet, "Data Combinada");
      const wbout = XLSX.write(newWorkbook, { bookType: "xlsx", type: "array" });
      const blob = new Blob([wbout], { type: "application/octet-stream" });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = nombreArchivo;
      a.click();
      URL.revokeObjectURL(url);
      Swal.fire({
        position: "top-end",
        icon: "success",
        title: mitextoCombinado,
        showConfirmButton: false,
        timer: 4000
      });
      // console.log('Archivos combinados exitosamente.');
    } catch (error) {
      console.error("Error al combinar archivos:", error);
    }

    await context.sync();
    });
  } catch (error) {
    console.error(error);
  }
}
export async function ejecutar() {
  Swal.fire({
    title: tituloPregunta,
    text: textoPregunta,
    icon: "question",
    showCancelButton: true,
    confirmButtonColor: "#0d6efd",
    cancelButtonColor: "#d33",
    cancelButtonText: `
    No
  `,
    focusCancel: true,
    confirmButtonText:respuestaPregunta

  }).then((result) => {
    if (result.isConfirmed) {
      run();
    }
  });
}
